export default function (){
    /**
     * UserAgentÊ£ÄÊµã
     * @name ua
     * @returns {Object} ËøîÂõû‰∏Ä‰∏™ËÆ∞ÂΩïua‰ø°ÊÅØÁöÑÂØπË±°
     *  isPC : boolean
     *  isMobile : boolean
     *  platform : 'pc' pc„ÄÅipadÁ≠âÂêÑÁ±ªÁßªÂä®Á´ØÂπ≥Âè∞ÂêçÁß∞
     *  browser : 'chrome' ÊµèËßàÂô®ÁöÑÂêçÁß∞
     *  version : 28 ÊµèËßàÂô®ÁöÑÁâàÊú¨
     *
     *  ÂêåÊó∂ÁªôhtmlÊ†πÂÖÉÁ¥†Ê∑ªÂä†
     *  .ua-pc / ua-mobile
     *  .ua-ipad / .ua-iphone / ... Ôºà‰∏ªË¶ÅÁî®‰∫éios‰∏ã‰∏Ä‰∫õÁâπÊÆäÈúÄË¶ÅÔºâ
     *  .browser-firefox / .browser-safari / ... Ôºà‰∏ªË¶ÅÁî®‰∫éfirefox,safari‰∏Ä‰∫õÁâπÊÆäÊÉÖÂÜµÔºâ
     *  .ie-8 / ... ÔºàÂ¶ÇÊûúÊòØIEÊµèËßàÂô®Ôºå‰ºöÈ¢ùÂ§ñÊúâie-ÁâàÊú¨Âè∑ÔºåÁî®Êù•ÁâπÊÆäÂ§ÑÁêÜIEÁâàÊú¨‰∏ãÁöÑÈóÆÈ¢òÔºâ
     *
     */
    var ua = window.navigator.userAgent.toLowerCase();
    var _ua = {
        isPC: true,
        isMobile: false,
        platform: "pc"
    };
    var cls = document.documentElement.className;

    //Âπ≥Âè∞Ê£ÄÊµã
    var mobileAgents = [
        "micromessenger",
        "android",
        "iphone",
        "symbianOS",
        "windows phone",
        "ipad",
        "ipod"
    ];
    var mobileAngntsAlias = [
        "wx",
        "android",
        "iphone",
        "symbian",
        "wp",
        "ipad",
        "ipod"
    ];
    var platformCheck = (function() {
        for (var i = 0; i < mobileAgents.length; i++) {
            if (ua.indexOf(mobileAgents[i]) > 0) {
                _ua.isPC = false;
                _ua.isMobile = true;
                _ua.platform = mobileAngntsAlias[i];
                cls += " ua-mobile ua-" + mobileAngntsAlias[i];
                document.documentElement.className = cls;
                break;
            }
        }
        if (_ua.isPC) {
            cls += " ua-" + "pc";
            document.documentElement.className = cls;
        }
    })();

    //ÊµèËßàÂô®Ê£ÄÊµã
    var pcAgents =
        /(micromessenger|opr|edge|firefox)[ \/]([\w.]+)/.exec(ua) || // Wechat & Oprea & Edge & firefox
        /(chrome)[ \/]([\w.]+)/.exec(ua) || // Chrome
        /(safari)[ \/]([\w.]+)/.exec(ua) || // Safari
        /(msie) ([\w.]+)/.exec(ua) || //IE7-10
        /(trident).+rv:(\w.)+/.exec(ua) ||
        []; //IE 11
    var pcAgentsAlias = {
        micromessenger: "wx",
        opr: "oprea",
        edge: "edge",
        firefox: "firefox",
        chrome: "chrome",
        safari: "safari",
        trident: "ie",
        msie: "ie"
    };
    var browserCheck = (function() {
        var browser = pcAgents[1];
        _ua.browser = pcAgentsAlias[browser];

        var version = pcAgents[2];
        _ua.version = parseInt(version);

        cls += " browser-" + _ua.browser;
        if (_ua.browser == "ie") {
            _ua.isIE = true;
            cls += " ie-" + _ua.version;
        }
        document.documentElement.className = cls;
    })();

    window._ua = _ua

    return _ua;
}